'use client';

import React, { useState, useRef } from 'react';
import { DataService } from '@/utils/api';

export default function ImageUpload() {
    const [selectedFile, setSelectedFile] = useState<File | null>(null);
    const [previewUrl, setPreviewUrl] = useState<string | null>(null);
    const [uploading, setUploading] = useState(false);
    const [message, setMessage] = useState<{ text: string; type: 'success' | 'error' } | null>(null);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
        if (event.target.files && event.target.files[0]) {
            const file = event.target.files[0];
            setSelectedFile(file);
            setPreviewUrl(URL.createObjectURL(file));
            setMessage(null);
        }
    };

    const handleUpload = async () => {
        if (!selectedFile) return;

        setUploading(true);
        setMessage(null);

        try {
            const result = await DataService.uploadImage(selectedFile);
            console.log('Image uploaded successfully:', result);
            setMessage({ text: 'Image uploaded for analysis!', type: 'success' });
            // Reset after success
            setTimeout(() => {
                setSelectedFile(null);
                setPreviewUrl(null);
                setMessage(null);
            }, 3000);
        } catch (error) {
            console.error('Upload failed:', error);
            setMessage({ text: 'Analysis failed. Please try again.', type: 'error' });
        } finally {
            setUploading(false);
        }
    };

    return (
        <div className="bg-[#0f172a] p-6 rounded-2xl border border-slate-800 shadow-xl mb-6">
            <h3 className="text-xl font-bold text-white mb-4">AI Pest Detection</h3>

            <div className="flex flex-col md:flex-row gap-6 items-start">
                {/* Upload Area */}
                <div className="flex-1 w-full">
                    <div
                        className={`border-2 border-dashed rounded-xl p-8 text-center transition-colors cursor-pointer
                            ${selectedFile ? 'border-emerald-500/50 bg-emerald-500/5' : 'border-slate-700 hover:border-slate-500 hover:bg-slate-800/50'}
                        `}
                        onClick={() => fileInputRef.current?.click()}
                    >
                        <input
                            type="file"
                            ref={fileInputRef}
                            onChange={handleFileSelect}
                            accept="image/*"
                            className="hidden"
                        />

                        {previewUrl ? (
                            <div className="relative h-48 w-full flex items-center justify-center">
                                <img
                                    src={previewUrl}
                                    alt="Preview"
                                    className="max-h-full max-w-full rounded-lg shadow-lg border border-slate-700"
                                />
                                <div className="absolute inset-0 bg-black/40 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity rounded-lg">
                                    <span className="text-white font-medium">Click to change</span>
                                </div>
                            </div>
                        ) : (
                            <div className="flex flex-col items-center gap-3 py-6">
                                <div className="w-12 h-12 bg-slate-800 rounded-full flex items-center justify-center text-slate-400">
                                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                                    </svg>
                                </div>
                                <div>
                                    <p className="text-slate-300 font-medium">Click to upload leaf image</p>
                                    <p className="text-slate-500 text-sm mt-1">Supports JPG, PNG</p>
                                </div>
                            </div>
                        )}
                    </div>
                </div>

                {/* Controls */}
                <div className="w-full md:w-64 flex flex-col gap-4">
                    <div className="bg-slate-900/50 p-4 rounded-xl border border-slate-800">
                        <h4 className="text-sm font-semibold text-slate-400 uppercase tracking-wider mb-2">Instructions</h4>
                        <ul className="text-sm text-slate-500 space-y-2 list-disc pl-4">
                            <li>Take a clear photo of the affected leaf</li>
                            <li>Ensure good lighting</li>
                            <li>Upload for instant AI analysis</li>
                        </ul>
                    </div>

                    <button
                        onClick={handleUpload}
                        disabled={!selectedFile || uploading}
                        className={`
                            py-3 px-6 rounded-xl font-bold text-white transition-all transform shadow-lg
                            ${!selectedFile || uploading
                                ? 'bg-slate-800 text-slate-500 cursor-not-allowed'
                                : 'bg-emerald-600 hover:bg-emerald-500 hover:scale-[1.02] shadow-emerald-500/25'
                            }
                        `}
                    >
                        {uploading ? (
                            <span className="flex items-center justify-center gap-2">
                                <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                                Analyzing...
                            </span>
                        ) : (
                            'Analyze Image'
                        )}
                    </button>

                    {message && (
                        <div className={`p-3 rounded-lg text-sm font-medium text-center ${message.type === 'success' ? 'bg-emerald-500/10 text-emerald-400 border border-emerald-500/20' : 'bg-red-500/10 text-red-400 border border-red-500/20'
                            }`}>
                            {message.text}
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
